<template>
    <About v-if="this.desc == 'about'"/>
    <Credits v-else-if="this.desc == 'credits'"/>
    <Privacy v-else-if="this.desc == 'privacy'"/>
    <TermsOfUse v-else-if="this.desc == 'termsOfUse'"/>
</template>

<script>
  import About from './About';
  import Credits from './Credits';
  import Privacy from './Privacy';
  import TermsOfUse from './Terms_of_use';
  
  export default {
    name: 'Option',
    components: {
        About, Credits, Privacy, TermsOfUse
    },
    data: function () {
        return {
            desc: ""
        }
    },
    created () {
        this.desc = this.$router.history.current.query.desc
    }
  }
</script>
