<template>
    <v-container fluid v-bind:style="{ backgroundColor: background_color, height: height }">
      <SubdomainsSelect :info="info" @showDomainsSelectPage="change_component" v-if="show_subom_page == 1"/>
      <template v-else>
        <h2>Domínio</h2>
        <div>
          <v-alert v-model="alert" dismissible type="error">
            Por favor, selecione um domínio
          </v-alert>
        </div>
        <v-row align="center">
          <v-col class="d-flex" cols="12" sm="4">
            <v-select v-model="select" :items="items" item-text="description" item-value="specificInfo" label="<Escolha o domínio>" persistent-hint return-object single-line></v-select>
          </v-col>
        </v-row>
        <v-card cols="12" flat v-bind:style="{ backgroundColor: background_color }" style="display: flex; justify-content: space-between">
          <v-col cols="6">
            <v-row style="height: 150px; float: left">
              <v-card flat class="ml-0" v-bind:style="{ backgroundColor: background_color }">
                <div>
                  <v-tooltip top>
                    <template v-slot:activator="{ on, attrs }">
                      <v-btn class="white--text text-capitalize" color="#00cc99" @click="verify_dominio" v-bind="attrs" v-on="on">
                        <v-icon>mdi-arrange-bring-forward</v-icon>
                      </v-btn>
                    </template>
                    <span>Subdomínios</span>
                  </v-tooltip>
                </div>
                <div class="mt-2">
                  <v-tooltip bottom>
                    <template v-slot:activator="{ on, attrs }">
                      <v-btn class="white--text text-capitalize" color="#172d44" v-bind="attrs" v-on="on">
                        <v-icon>mdi-exclamation</v-icon>
                      </v-btn>
                    </template>
                    <span>Acerca</span>
                  </v-tooltip>
                </div>
              </v-card>
              <v-card flat class="ml-2" v-bind:style="{ backgroundColor: background_color }">
                <div>
                  <v-tooltip top>
                    <template v-slot:activator="{ on, attrs }">
                      <v-btn class="white--text text-capitalize" color="#00cc99" @click="reload" v-bind="attrs" v-on="on">
                        <v-icon>mdi-broom</v-icon>
                      </v-btn>
                    </template>
                    <span>Limpar</span>
                  </v-tooltip>
                </div>
                <div class="mt-2">
                  <v-tooltip bottom>
                    <template v-slot:activator="{ on, attrs }">
                      <v-btn class="white--text text-capitalize" color="#172d44" v-bind="attrs" v-on="on">
                        <v-icon>mdi-help</v-icon>
                      </v-btn>
                    </template>
                    <span>Ajuda</span>
                  </v-tooltip>
                </div>
              </v-card>
              <v-card flat class="ml-2" v-bind:style="{ backgroundColor: background_color }">
                <div>
                  <v-tooltip top>
                    <template v-slot:activator="{ on, attrs }">
                      <v-btn class="white--text text-capitalize" color="#00cc99" v-bind="attrs" v-on="on">
                        <v-icon>mdi-flag-checkered</v-icon>
                      </v-btn>
                    </template>
                    <span>Terminar</span>
                  </v-tooltip>
                </div>
              </v-card>              
            </v-row>
          </v-col>
        </v-card>
      </template>
    </v-container>
</template>

<script>
  import SubdomainsSelect from './Subdomains_select.vue'
  export default {
    name: 'Domains_select',

    components:{
      SubdomainsSelect
    },

    data: function () {
        return {
            background_color: "#F7F7F7", height: "1000px", select: {description: ""}, specificInfo: {}, items: [], alert: false,
            user: {
              userId: "",
              userName: "",
              name: "",
              email: "",
              gender: "",
              degree: "",
              user_type: "",
            },
            domains: [], show_subom_page: 0, info: {}
        }
    },

    async created(){
      await this.load_attributes()
    },

    methods: {
      async change_component(){
        this.show_subom_page = 0
      },
      async verify_dominio(){
        if(this.select.description == ""){
          this.alert = true
        }
        else{
          this.info = {
            domain: this.select.description,
            subdomains: this.select.specificInfo.subdomains,
            id: this.user.userId,
            username: this.user.userName,
            name: this.user.name,
            email: this.user.email,
            gender: this.user.gender,
            degree: this.user.degree,
            user_type: this.user.user_type
          }
          this.show_subom_page = 1
        }
      },
      async load_attributes(){
        this.user.userId = this.$router.history.current.query.id
        this.user.userName = this.$router.history.current.query.username
        this.user.name = this.$router.history.current.query.name
        this.user.email = this.$router.history.current.query.email
        this.user.gender = this.$router.history.current.query.gender
        this.user.degree = this.$router.history.current.query.degree                
        this.user.user_type = this.$router.history.current.query.user_type


        this.$http.get('https://leonardo.di.uminho.pt/api/v0/evaluation/getDomains?idUser=' + this.user.userId)
          .then(dados => {
            let doms = eval(dados.data)

            for(let i = 0; i<doms.length; i++) {
              for(let j = 0; j<doms[i].domains.length; j++){
                this.items.push({
                  description: doms[i].domains[j].description,
                  specificInfo: {
                    study_cycle: doms[i].domains[j].study_cycle,
                    scholarity: doms[i].domains[j].scholarity,
                    subdomains: doms[i].domains[j].subdomains
                  }
                })
              }
            }
          }) 
          .catch(error => { throw(error) })
      },
      async reload(){
        Object.assign(this.$data, this.$options.data.call(this))
        await this.load_attributes()
      }
    }
  }
</script>
